
import Vue from 'vue'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'

import Upload from 'c%/upload'
import AddPopup from './add-popup.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default{
mixins: [ common,wxjssdk ],
components:{
Upload,
Flexbox,
AddPopup,
FlexboxItem
},
data () {
return {
info: {},
popup_add: false
}
},
mounted () {
this.init_data()
},
methods: {
setShare () {
let info = this.info
let [title,img,desc,link] = [util.sprintf('%s-%s',info.student.student_name,info.title),'',info.content,'']

link = location.origin + location.pathname + location.hash.replace(/\/(\D|\d){0,10000}/,'/growth_contrast/'+this.$route.params.id)

if(info.edu_growup_item.length > 0){
let obj = info.edu_growup_item.find(i=>i.edu_growup_pic.length>0)
if(obj){
img = obj.edu_growup_pic[0].url
}
}




this.setWxShare(title,img,desc,link)
},
init_data () {
let uri = util.sprintf('share/edu_growup?eg_id=%s&with=edu_growup_item.edu_growup_pic,student,one_class',this.$route.params.id)
this.$rest(uri).get()
.success(data=>{
this.info = data
this.setShare()
}).error(response=>{
this.toast(response.body.messsage,'text')
})
},
UploadList (item,position) {
let [list,attachments,img_list] = [item.edu_growup_pic,[],[]]

if(list.length > 0){
attachments = list.filter(l=>l.position==position)

if(attachments.length > 0){
attachments.forEach(a=>{
img_list.push(a.url)
})
}
}
return img_list
},
preview (current,list) {
this.previewImage(current,list)
}
},
computed: {
abilities () {
let [lesson_config,ability] = [this.$store.state.user.info.global.lesson_config,[]]
if(lesson_config && lesson_config.ability){
ability = lesson_config.ability
}
return ability
}
}
}



