
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Upload from 'c%/upload'
import SelectFile from 'c%/SelectFile.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


const emptyObject = {
bid: 0,
title: '',
before_content: '',
after_content: '',
edu_growup_pic: []
}

export default{
mixins: [common],
directives: {
TransferDom
},
components:{
Cell,
Popup,
Group,
Upload,
XInput,
XButton,
Divider,
Flexbox,
FlexboxItem,
XTextarea,
SelectFile
},
props: {

},
data () {
return {
popup_add: false,
popup_content: false,
growup_item: util.copy(emptyObject),
before_uploadList: [],
after_uploadList: [],
content_text: '',
content_type: '',
}
},
watch: {
popup_add: function (val) {
this.growup_item = util.copy(emptyObject)
this.before_uploadList = []
this.after_uploadList = []
}
},
methods: {
show_popup (type) {
this.content_type = type
if(type=='before_content'){
this.content_text = this.growup_item.before_content
this.uploadList = this.before_uploadList
}else{
this.content_text = this.growup_item.after_content
this.uploadList = this.after_uploadList
}
this.popup_content = true
},
submit_content () {
let [content_text,content_type,uploadList,edu_growup_pic,position] = [this.content_text,this.content_type,this.uploadList,this.growup_item.edu_growup_pic,'before']
this.growup_item[content_type] = content_text
if(content_type == 'before_content'){
this.before_uploadList = uploadList
}else{
this.after_uploadList = uploadList
position = 'after'
}
uploadList.forEach(u=>{
if(typeof edu_growup_pic.find(a=>a.url===u.url)==='undefined'){
edu_growup_pic.push({
url: u.file_url,
position: position
})
}
})
this.uploadList = []
this.content_text = ''
this.popup_content = false
},
submit_growup_item () {
this.growup_item.bid = this.bid$[0]
this.$rest('edu_growups').add_url_param(this.$route.params.id,'items').post(this.growup_item)
.success(response=>{
this.toast('添加成功','success')
this.$emit('success')
setTimeout(()=>{
this.popup_add = false
},800)
}).error(response=>{
this.toast(response.body.message,'text')
})
},
UploadList (position) {
let [list,edu_growup_pic] = [this.growup_item.edu_growup_pic,[]]
if(list.length > 0){
edu_growup_pic = list.filter(l=>l.position==position)
}
return edu_growup_pic
},
},
computed: {
style () {
return {
top: `${this.headerHeight}px`,
height: `calc(100% - ${this.headerHeight}px)`
}
},
upload_header(){
return {
'x-token':this.$store.state.user.token,
'x-file-key':'file'
}
},
upload_post(){
return {
mod:'growup_item_upload'
}
},
}
}



